import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, FormBuilder, Validators } from '@angular/forms';
import { AuthGuard } from '../auth.guard';

@Component({
  selector: 'app-signup',
  templateUrl: './signup.component.html',
  styleUrls: ['./signup.component.scss']
})
export class SignupComponent implements OnInit {

  signupForm: FormGroup;
  isSubmitted = false;

  constructor(private fb: FormBuilder, private auth: AuthGuard) {
    this.signupForm = this.fb.group({
      username: new FormControl('', [Validators.required, Validators.minLength(3), Validators.maxLength(12)]),
      password: new FormControl('', [Validators.required, Validators.minLength(1), Validators.maxLength(6), Validators.pattern('/^(?=.*\d)/')]),
      email: new FormControl('', [Validators.required, Validators.email])
    })
  }

  ngOnInit() {
  }
  hi() {
    // alert("signup");
    this.isSubmitted = true;
    console.log(this.signupForm);
    console.log(this.signupForm.value);
    if (this.signupForm.value.username === 'Manikandan') {
      this.auth.isLoggedIn = true;
    }
  }
}
