import { Component, OnInit } from '@angular/core';
import * as XLSX from 'xlsx';
import { EmployeeService } from '../employee.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-employee',
  templateUrl: './employee.component.html',
  styleUrls: ['./employee.component.scss']
})
export class EmployeeComponent implements OnInit {

  employeeArray = [];
  themeSwitch = true;

  constructor(private emp: EmployeeService, private route: Router) {
    this.employeeArray = this.emp.getAllEmployee();
  }

  ngOnInit() {
  }

  xlxs() {
    /* generate worksheet */
    const ws: XLSX.WorkSheet = XLSX.utils.json_to_sheet(this.employeeArray);

    /* generate workbook and add the worksheet */
    const wb: XLSX.WorkBook = XLSX.utils.book_new();
    XLSX.utils.book_append_sheet(wb, ws, 'Sheet1');

    /* save to file */
    XLSX.writeFile(wb, 'SheetJS.xlsx');
  }

  theme() {
    this.themeSwitch = !this.themeSwitch;
  }
  employee_btn(index) {
    // alert(index);
    this.emp.selectedEmployee = index;
    this.route.navigateByUrl('/employee-list');
  }

}
