import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'language'
})
export class LanguagePipe implements PipeTransform {
  languageSet = {
    'TAMIL': {
      'HELLO': 'வணக்கம்',
      'HOME': 'வீடு',
      'SIGNUP':'இணைந்ததற்கு',
      'LOGIN':'இணைந்ததற்கு',
      'PAGENOTFOUND':'பக்கம் கண்டுபிடிக்கப்படவில்லை',
      'USERNAME':'பயனர் பெயர்',
      'PASSWORD':'கடவுச்சொல்',
      'SUBMIT':'சமர்ப்பிக்க',
      'EMPLOYEE':'ஊழியர்',
      'DUMMY':'போலி'

    },
    'ENGLISH': {
      'HELLO': 'Hello',
      'HOME': 'Home',
      'SIGNUP':'Signup',
      'LOGIN':'login',
      'PAGENOTFOUND':'Page Not Found',
      'USERNAME':'UserName',
      'PASSWORD':'Password',
      'SUBMIT':'Submit',
      'EMPLOYEE':'Employee',
      'DUMMY':'Dummy'

    },
    'HINDI': {
      'HELLO': 'नमस्कार',
      'HOME': 'घर',
      'SIGNUP':'साइन अप करें',
      'LOGIN':'लॉग इन करें',
      'PAGENOTFOUND':'पृष्ठ नहीं मिला',
      'USERNAME':'उपयोगकर्ता नाम',
      'PASSWORD':'कुंजिका',
      'SUBMIT':'प्रस्तुत',
      'EMPLOYEE':'कर्मचारी',
      'DUMMY':'डमी'
    },
  }

  transform(value: any, args?: string): any {
    console.log(value);
    let language = localStorage.getItem('LANGUAGE');
    if(args){
      language = args;
    }
    if(this.languageSet[language][value]){
      return this.languageSet[language][value];
    }
    return value;
  }

}
